

.arcticmodal-overlay,
.arcticmodal-container {
	position: fixed;
	z-index: 1000;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}
.arcticmodal-container {
	overflow: auto;
	margin: 0;
	padding: 0;
	border-collapse: collapse;
	border: 0;
}
*:first-child + html .arcticmodal-container {
	height: 100%;
}
.arcticmodal-container_i {
	height: 100%;
	margin: 0 auto;
}
.arcticmodal-container_i2 {
	margin: 0;
	padding: 24px 0;
	vertical-align: middle;
	border: 0;
}
.arcticmodal-error {
	padding: 20px;
	color: #fff;
	border-radius: 10px;
	background: #000;
}
.arcticmodal-loading {
	width: 80px;
	height: 80px;
	border-radius: 10px;
	background: #000 url(../img/loading.gif) no-repeat 50% 50%;
}

/* Slider */
.slick-slider {
	position: relative;
	display: block;
	box-sizing: border-box;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;

	 -khtml-user-select: none;
	-webkit-tap-highlight-color: transparent;
	-ms-touch-action: pan-y;
	    touch-action: pan-y;
	-webkit-touch-callout: none;
}
.slick-list {
	position: relative;
	display: block;
	overflow: hidden;
	margin: 0;
	padding: 0;
}
.slick-list:focus {
	outline: none;
}
.slick-list.dragging {
	cursor: pointer;
	cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
	-webkit-transform: translate3d(0, 0, 0);
	    -ms-transform: translate3d(0, 0, 0);
	        transform: translate3d(0, 0, 0);
}
.slick-track {
	position: relative;
	top: 0;
	left: 0;
	display: block;
}
.slick-track:before,
.slick-track:after {
	display: table;
	content: '';
}
.slick-track:after {
	clear: both;
}
.slick-loading .slick-track {
	visibility: hidden;
}
.slick-slide {
	display: none;
	float: left;
	height: 100%;
	min-height: 1px;
}
[dir='rtl'] .slick-slide {
	float: right;
}
.slick-slide img {
	display: block;
}
.slick-slide.slick-loading img {
	display: none;
}
.slick-slide.dragging img {
	pointer-events: none;
}
.slick-initialized .slick-slide {
	display: block;
}
.slick-loading .slick-slide {
	visibility: hidden;
}
.slick-vertical .slick-slide {
	display: block;
	height: auto;
	border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
	display: none;
}
.slick-loading .slick-list {
	background: #fff url('../img/ajax-loader.gif') center center no-repeat;
}

/* Arrows */
.slick-prev,
.slick-next {
	position: absolute;
	z-index: 2;
	top: 50%;
	width: 52px;
	height: 52px;
	margin-top: -26px;
	color: transparent;
	font-size: 0;
	cursor: pointer;
	background: #e5e5e5;
	transition: all .3s;
}
.slick-prev {
	left: 0;
}
.slick-prev:after {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	margin: auto;
	background: url('../img/sprite.png') no-repeat -69px -630px;
	width: 12px;
	height: 20px;
}
.slick-next {
	right: 0;
}
.slick-next:after {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	margin: auto;
	background: url('../img/sprite.png') no-repeat -86px -630px;
	width: 12px;
	height: 20px;
}

/* Dots */
.slick-dots {
	position: absolute;
	z-index: 10;
	bottom: 20px;
	display: block;
	width: 100%;
	padding-right: 17px;
	font-size: 0;
	text-align: right;
}
.slick-dots li {
	position: relative;
	display: inline-block;
	margin: 0 3px;
	cursor: pointer;
}
.slick-dots li button {
	display: block;
	width: 14px;
	height: 14px;
	color: transparent;
	font-size: 0;
	line-height: 0;
	cursor: pointer;
	border: 3px solid rgba(255, 255, 255, .50);
	border-radius: 50%;
	outline: none;
	background: transparent;
}
.slick-dots li.slick-active button {
	border: 3px solid #fff;
	background: #fff;
}

