/*-------------------- global styles --------------------*/
html {
	min-width: 320px;
	background-color: #f5f5f5;
}
body {
	min-width: 1140px;
	height: 100%;
	color: #000;
	font-family: 'Roboto', sans-serif;
}
a {
	color: #000;
	text-decoration: none;
	-webkit-transition: all .3s;
	        transition: all .3s;
}
.wrap {
	position: relative;
	overflow: hidden;
	width: 1140px;
	margin: 0 auto;
}
.btn {
	position: relative;
	display: inline-block;
	height: 30px;
	padding: 0 20px;
	color: #005ead;
	font-size: 13px;
	font-weight: 300;
	line-height: 30px;
	vertical-align: top;
	-webkit-transition: all .3s;
	transition: all .3s;
	border: 1px solid #e5e5e5;
	border-radius: 2px;
	background: transparent;
}
.btn:hover {
	color: #f89728;
}
.btn-2 {
	position: relative;
	display: inline-block;
	vertical-align: top;
	color: #8e8e8e;
	font-size: 13px;
	font-weight: 700;
	height: 40px;
	line-height: 40px;
	text-align: center;
	padding: 0 10px;
	border: 1px solid #8e8e8e;
	border-radius: 2px;
	background: transparent;
	-webkit-transition: all .3s;
	transition: all .3s;
}
.btn-2:hover {
	color: #f89728;
	border: 1px solid #f89728;
}
.btn-3 {
	position: relative;
	display: inline-block;
	vertical-align: top;
	color: #fff;
	font-size: 13px;
	font-weight: 700;
	height: 40px;
	line-height: 40px;
	text-align: center;
	padding: 0 10px;
	border: 1px solid #8e8e8e;
	border-radius: 2px;
	background: #005ead;
	-webkit-transition: all .3s;
	transition: all .3s;
	box-shadow: 3px 0 9px 1px rgba(0, 0, 0, .15);
}
.btn-3:hover {
	background: rgba(0, 94, 173, 0.88);
}
/*title-bordered*/
.title-bordered {
	padding: 14px 22px;
	text-align: left;
	border: 1px solid #e5e5e5;
	background: #fff;
}
.title-bordered .title {
	float: left;
	font-size: 18px;
	font-weight: 700;
}
.title-bordered .toggle-filter {
	float: left;
	display: none;
	/*display: inline-block;*/
	color: #fff;
	font-size: 13px;
	line-height: 33px;
	font-weight: 700;
	padding: 0 10px;
	text-align: center;
	min-width: 84px;
	height: 33px;
	background: #f89728;
	border-radius: 2px;
}
.title-bordered .sort {
	float: right;
}
.title-bordered .sort .sort-block {
	display: inline-block;
	vertical-align: middle;
}
.title-bordered .sort .sort-block label {
	display: inline-block;
	vertical-align: middle;
	color: #737373;
	font-size: 13px;
	font-weight: 300;
	margin-right: 5px;
}
.title-bordered .sort .sort-block .select-wrap {
	display: inline-block;
	vertical-align: middle;
}

/*title-w-link*/
.title-w-link {
	margin-bottom: 20px;
}
.title-w-link .title {
	display: inline-block;
	margin-right: 10px;
	color: #000;
	font-size: 18px;
	font-weight: 700;
	vertical-align: middle;
}
.title-w-link .btn {
	vertical-align: middle;
}
/*news-preview*/
.news-preview {
	display: inline-block;
	width: 263px;
	text-align: left;
	vertical-align: top;
	border: 1px solid #e5e5e5;
	background-color: #fff;
}
.news-preview .img-date-block {
	position: relative;
	overflow: hidden;
	height: 131px;
}
.news-preview .img-date-block img {
	position: absolute;
	top: 50%;
	left: 50%;
	width: auto;
	min-width: 100%;
	max-width: 5000px;
	height: auto;
	min-height: 100%;
	-webkit-transition: .3s all;
	transition: .3s all;
	-webkit-transform: translate(-50%, -50%) scale(1);
	-ms-transform: translate(-50%, -50%) scale(1);
	transform: translate(-50%, -50%) scale(1);
}
.news-preview:hover .img-date-block img {
	-webkit-transform: translate(-50%, -50%) scale(1.1);
	-ms-transform: translate(-50%, -50%) scale(1.1);
	transform: translate(-50%, -50%) scale(1.1);
}
.news-preview .img-date-block .date {
	position: absolute;
	bottom: 0;
	left: 0;
	height: 25px;
	padding: 0 10px;
	color: #fff;
	font-size: 12px;
	font-weight: 300;
	line-height: 25px;
	background-color: #005ead;
}
.news-preview .text-block {
	padding: 10px 20px;
	color: #000;
	font-size: 14px;
	font-weight: 300;
	-webkit-transition: .3s all;
	transition: .3s all;
	border-top: 1px solid #e5e5e5;
	/*word-break: break-all;*/
}
.news-preview:hover .text-block {
	color: #005ead;
}

/*breadcrumbs*/
.breadcrumbs {
	background: #f5f5f5;
}
.breadcrumbs .wrap {
	padding: 15px 0;
	font-size: 13px;
	font-weight: 300;
}
.breadcrumbs a {
	position: relative;
	display: inline-block;
	margin-right: 30px;
	color: #737373;
	line-height: 21px;
	vertical-align: middle;
}
.breadcrumbs a:hover {
	color: #f89728;
}
.breadcrumbs a:after {
	position: absolute;
	top: 0;
	right: -16px;
	bottom: 0;
	margin: auto;
	color: #ccc;
	font-size: 13px;
	content: '/';
}
.breadcrumbs span {
	display: inline-block;
	color: #737373;
	vertical-align: middle;
}

/*page-title*/
.page-title {
	padding: 15px 0;
	color: #000;
	font-size: 28px;
	font-weight: 700;
	line-height: 1;
}
.page-title .edit-link {
	cursor: pointer;
	display: inline-block;
	vertical-align: top;
	margin-top: 1px;
	margin-right: 5px;
	background: url('../img/sprite.png') no-repeat -479px -484px;
	width: 25px;
	height: 25px;
}
.page-title sup {
	top: -1em;
	color: #737373;
	font-size: 12px;
	font-weight: 300;
}

/*paginations*/
.paginations {
	margin-top: 20px;
}
.paginations .pagination {
	font-size: 0;
	text-align: center;
}
.paginations .pagination li {
	display: inline-block;
	vertical-align: top;
}
.paginations .pagination li a {
	cursor: pointer;
	position: relative;
	display: block;
	width: 40px;
	height: 40px;
	text-align: center;
	padding: 0 1px;
	border: 1px solid #ccc;
	border-left: none;
	line-height: 38px;
	color: #005ead;
	font-size: 14px;
	font-weight: 300;
}
.paginations .pagination li.disabled a {

}
.paginations .pagination li.active a {
	background: #e5e5e5;
}
.paginations .pagination li span {
	position: relative;
	display: block;
	width: 40px;
	height: 40px;
	text-align: center;
	padding: 0 1px;
	border: 1px solid #ccc;
	border-left: none;
	line-height: 38px;
	color: #000;
	font-size: 14px;
	font-weight: 300;
}
.paginations .pagination li.disabled span {

}
.paginations .pagination li.active span {
	background: #e5e5e5;
}
.paginations .pagination li .prev {
	border-left: 1px solid #ccc;
}
.paginations .pagination li .prev:after {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	width: 10px;
	height: 20px;
	background: url('../img/sprite.png') no-repeat -356px -371px;
}
.paginations .pagination li.disabled .prev:after {
	background: url('../img/sprite.png') no-repeat -341px -371px;
}
.paginations .pagination li .next {
	/*border-left: 1px solid #ccc;*/
}
.paginations .pagination li .next:after {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	width: 10px;
	height: 20px;
	background: url('../img/sprite.png') no-repeat -372px -371px;
}
.paginations .pagination li.disabled .next:after {
	background: url('../img/sprite.png') no-repeat -387px -371px;
}

/*formated-text*/
.formated-text {
	font-size: 14px;
	line-height: 21px;
	color: #000;
	font-weight: 300;
}
.formated-text p {
	color: #000;
	font-weight: 300;
	margin-bottom: 20px;
}
.formated-text img {
