/*-------------------- modals --------------------*/
/*modal-callback*/
.modal {
	position: relative;
	width: 500px;
	background: #fff;
}
.modal .modal-top {
	position: relative;
	background: #e5e5e5;
	padding: 16px 70px 16px 30px;
}
.modal .modal-top .modal-title {
	color: #000;
	font-size: 18px;
	font-weight: 500;
	line-height: 20px;
}
.modal .modal-top .modal-close {
	cursor: pointer;
	position: absolute;
	top: 17px;
	right: 30px;
	background: url('../img/sprite.png') no-repeat -52px -458px;
	width: 22px;
	height: 22px;
}
.modal .modal-top .modal-close:hover {
	background: url('../img/sprite.png') no-repeat -84px -459px;
	width: 22px;
	height: 22px;
}
.modal .modal-center {
	padding: 16px 30px;
}
.modal .modal-center .modal-text {
	color: #737373;
	font-size: 14px;
	font-weight: 300;
	line-height: 20px;
}
.modal .modal-center .field-row {
	margin-top: 20px;
	font-size: 0;
}
.modal .modal-center .field-row .label-wrap {
	display: inline-block;
	vertical-align: top;
	width: 100px;
	padding: 7px 10px 0 0;
}
.modal .modal-center .field-row .label-wrap label {
	display: block;
	color: #000;
	font-size: 14px;
	font-weight: 500;
	line-height: 20px;
}
.modal .modal-center .field-row .field-wrap {
	display: inline-block;
	vertical-align: top;
	width: calc(100% - 100px);
	position: relative;
}
.modal .modal-center .field-row .field-wrap input,
.modal .modal-center .field-row .field-wrap textarea {
	display: block;
	width: 100%;
	height: 40px;
	background: #fff;
	border: 1px solid #ccc;
	border-radius: 2px;
	transition: border .3s;
	padding: 5px 15px;
	color: #000;
	font-size: 14px;
	font-weight: 300;
	line-height: 21px;
}
.modal .modal-center .field-row .field-wrap input {

}
.modal .modal-center .field-row .field-wrap textarea {
	height: 100px;
}
.modal .modal-center .field-row .field-wrap input:focus,
.modal .modal-center .field-row .field-wrap textarea:focus {
	border: 1px solid #005ead;
}
.modal .modal-bottom {
	border-top: 1px solid #e5e5e5;
	padding: 20px 30px;
	text-align: center;
}
.modal .modal-bottom button {
	min-width: 225px;
}
@media only screen and (max-width: 500px) {
	.modal {
		width: 320px;
	}
	.modal .modal-top {
		padding: 12px 55px 12px 15px;
	}
	.modal .modal-top .modal-close {
		top: 12px;
		right: 15px;
	}
	.modal .modal-center {
		padding: 12px 15px;
	}
	.modal .modal-bottom {
		padding: 12px 15px;
	}
}


